.link-ani:after {
  content: "";
  position: absolute;
  width: 100%;
  transform: scaleX(0);
  height: 0.1rem;
  bottom: -0.2rem;
  left: 0;
  background-color: #5C6585;
  transform-origin: bottom right;
  transition: transform 0.25s ease-out;
}
