.footer {
    @include rel-block;
    padding: 12rem 0 7rem 0;
    background: $blue;

    @include fullHD {
        padding-top: 16rem;
    }

    .logo {
        @include rel-block;
        margin-bottom: 2.6rem;
    }

    .row {
        .col {
            &:last-child {
                display: flex;
                flex-direction: column;
                justify-content: space-between;
            }
        }
    }

    .txt {
        @include rel-block;

        p,
        span {
            text-align: left;
        }

        span {
            font-style: normal;
            font-weight: 700;
            font-size: 5rem;
            line-height: 110%;
            letter-spacing: -0.02em;
            @include rel-block;
            margin-bottom: 2.6rem;
            font-family: $font;
            color: $white;

            @include fullHD {
                font-size: 6.4rem;
            }
        }

        p {
            font-style: normal;
            font-weight: 500;
            font-size: 1.6rem;
            line-height: 150%;
            color: $white;
            max-width: 56rem;
            margin-bottom: 5rem;

            @include fullHD {
                font-size: 2rem;
            }
        }

        button {
            @include link;

            &:hover {
                transform: translate(0px, -.6rem);
            }
        }
    }

    // NAV
    nav {
        @include rel-block;
        width: 100%;

        ul {
            display: flex;
            flex-wrap: wrap;
            width: 100%;
            justify-content: flex-end;

            li {
                margin-left: 6.4rem;

                &:first-child {
                    margin-left: 0;
                }

                &:last-child {
                    display: none;
                }

                a {
                    font-weight: 600;
                    font-size: 1.6rem;
                    line-height: 120%;

                    color: $white;
                    font-family: $font;
                    @include rel-block;

                    &:after {
                        display: block;
                        content: '';
                        position: relative;
                        width: 100%;
                        height: .2rem;
                        margin-top: .2rem;
                        transform: scaleX(0);
                        background-color: $white;
                        transform-origin: bottom right;
                        transition: transform 0.25s ease-out;
                    }

                    &:hover {
                        color: $white;

                        &:after {
                            transform: scaleX(1);
                            transform-origin: bottom left;
                        }
                    }
                }
            }
        }
    }

    // SOCIAL
    .social {
        display: flex;
        flex-wrap: wrap;
        width: 100%;
        justify-content: flex-end;

        li {
            margin-left: 3.2rem;

            &:first-child {
                margin-left: 0;
            }

            a {
                @include rel-block;
                @include link;

                &:hover {
                    transform: translate(0px, -.6rem);
                }
            }
        }
    }


    .copy {
        @include rel-block;
        margin-top: 12rem;
        border-top: .1rem solid $white;
        padding-top: 2.4rem;

        font-family: $font;
        font-style: normal;
        font-weight: 600;
        font-size: 1.6rem;
        line-height: 120%;
        color: $white;
    }


}